.c-Banner a[href],
.c-NavBar--mobile a[href],
.c-FooterPanel a[href] {
  color: inherit;
}
.c-Banner--copyright a[href]:hover,
.c-NavBar--contrib a[href]:hover,
.c-NavBar--mobile a[href]:hover,
.c-FooterPanel a[href]:hover,
.c-Banner--copyright a[href]:focus,
.c-NavBar--contrib a[href]:focus,
.c-NavBar--mobile a[href]:focus,
.c-FooterPanel a[href]:focus {
  text-decoration: underline;
}
